/* HTML Tags */
html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    box-sizing: border-box;
    height: 100%;
}

body {
  background: $body-background;
  color: $text;
  font-size: 100%;
  line-height: 160%;

  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height:100%;
  height:auto !important;/* combatting livefyre setting, so flexbox works in Safari 9.3 */
}

main {
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;	
}

a {
  color: $link;
  cursor: pointer;
  text-decoration: none;
  transition: none $speed $easing;
  &:hover {
    color: $link-hover;
    text-decoration: underline;
  }
  &:active {
    color: $link-active;
  }
  &:visited {
    color: $link-visited;
  }
}

small {
  font-size: $size-small;
}

code {
  border-radius: 2px;
  border: 1px solid #d3d6db;
  background-color: $pre-background;
  display: inline-block;
  font-size: 0.875rem;
  padding: 3px 7px;
}

pre {
    overflow: auto;
    padding: 16px;
    margin: 10px 0 19px;
    font-size: 0.875rem;
    line-height: 1.5;
    direction: ltr;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    -webkit-overflow-scrolling: touch;
    border: 1px solid #d3d6db;
    background-color: $pre-background;
  
  & code {
    border:0;    
    padding:0;
  }
}

p code{
    font-size: 0.8rem;
}

.theme_night pre, .theme_night code {
    border-color:#333;
    background-color: $pre-dark-background;
    color: $pre-dark
}

/* Generic Extends */
@define-mixin block {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.container {
    margin: 0 auto;
    width: 100%;
    max-width: 1600px;
    position: relative;
}

.container::before, .container::after {
    content: " ";
    display: table;
}

.container::after {
    clear: both;
}

ul, ol {
    margin: 0;
    padding: 0;
    margin-bottom: 16px;
}

h1,h2,h3,h4,h5,h6 {
    font-style: normal;
    font-weight: 300;
    line-height: 1.3;
    text-rendering: optimizeLegibility;
    margin-bottom: 12px;
    margin-top: 10px;
    word-wrap: break-word;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 4px;
}

h2 {
    font-size: 1.75rem;
    margin-top: 32px;
}

h3 {
    font-size: 1.188rem;
    margin-top: 30px;
    margin-bottom: 18px;
}

h4 {
    font-size: 1rem;
    margin-top: 36px;
    margin-bottom: 6px;
}

h5 {
    font-size: 0.9rem;
    letter-spacing: 1px;
    margin-top: 36px;
    margin-bottom: 6px;
}

h6 {
    font-size: 0.8rem;
    letter-spacing: 1px;
    margin-top: 36px;
    margin-bottom: 6px;
}

select {
    display: inline-block;
    overflow: auto;
    box-sizing: border-box;
    margin: 0;
    padding: 0 30px 0 6px;
    vertical-align: middle;
    height: 28px;
    border: solid 1px #e3e3e3;
    font-size: 0.75rem;
    line-height: 16px;
    outline: 0;
    text-overflow: ellipsis;
    -webkit-appearance: none;
    -moz-appearance: none;
    cursor: pointer;
    background-image: linear-gradient(45deg, transparent 50%, #707070 50%),linear-gradient(135deg, #707070 50%, transparent 50%);
    background-position: calc(100% - 13px) 11px,calc(100% - 8px) 11px;
    background-size: 5px 5px, 5px 6px;
    background-repeat: no-repeat;
    background-color: #fcfcfc;
}

select:focus {
    border-color: #aeb1b5;
    outline: 1px solid $blue;
}

#footer select:focus {
    outline: 2px solid $blue;
}

select::-ms-expand {
    display: none;
}

html.highContrast select {
    -webkit-appearance:menulist;
    -moz-appearance: menulist;
    background-image: none;
    padding-right: 0;
}

html.highContrast select::-ms-expand {
    display: block;    
}

input[type=search] {
    border: 1px solid #A9A9A9;
    border-radius:0;
    -webkit-appearance: none;
    outline:none;
}

html.theme_night body {
    background-color: #1a1a1a;
    color: #ccc;
}

html.theme_night input[type=search] {
    color: #333;
}

.theme_night select {
    background-color: #1a1a1a;
    border-color: #2e2e2e
}

option {
    padding: 0;
    margin: 0
}

hr {
    height: 0;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #e3e3e3
}

.theme_night hr {
    border-color: #2e2e2e
}

input::-ms-clear {
    display: none;
}