.share-container {
    color: #e3e3e3;
    background-color: #fff;
    font-size: 12px;
    border: 1px solid #e3e3e3;
    border-radius: 2px;
    padding-left: 5px;
    padding-right: 5px;
    /*width: 85px;*/
    display: none;
    position: absolute;
    top: 50px;
    left: 375px;
    z-index: 1;
}

.share-separator {
    border-bottom: 1px solid #e3e3e3;
}

.share-wechat {
    cursor: text;
}

#shareWithQrcode {
    height: 105px;
}

.theme_night .share-container {
    border-color: #2e2e2e;
    background-color: #1a1a1a;
}

.theme_night .share-separator {
    border-bottom: 1px solid #2e2e2e;
}

.theme_night .share-container:before {
    background-color: rgba(0, 0, 0, 0);
    border-color: #2e2e2e;
}

.share-container:before {
    display: block;
    position: absolute;
    z-index: 2;
    content: '';
    border: solid;
    background-color: #fff;
    width: 0.66666em;
    height: 0.66666em;
    border-color: inherit;
    border-width: 0 1px 1px 0;
    -webkit-transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
    transition: background-color .1s ease,opacity .1s ease,color .1s ease,box-shadow .1s ease;
    -webkit-transform: translateY(-50%) translateX(50%) rotate(-135deg);
    -ms-transform: translateY(-50%) translateX(50%) rotate(-135deg);
    transform: translateY(-50%) translateX(50%) rotate(-135deg);
}

.share-container > div {
    color: $blue;
    display: block;
    height: 25px;
}

.theme_night .share-container > div {
    color: #00a4f3;
}

.share-container a {
    color: #333;
}

.share-container a:hover {
    color: #0070a7;
}

.share-container a.share-wechat:hover {
    color: #333;
}

.theme_night .share-container a {
    color: #ccc;
}

.theme_night .share-container a:hover {
    color: #0070a7;
}

.theme_night .share-container a.share-wechat:hover {
    color: #ccc;
}