.metadata {
    display: block;
    overflow: hidden;
    margin: 0 0 24px;
}

.metadata .info {
    font-size: 0.8125rem;
}

.metadata .meta {
    color: $text-subtle;
}

.theme_night .metadata .meta {
    color: #828282;
}

.theme_night .metadata .loading,
.metadata .loading {
    color: transparent;
	-webkit-transition: color 200ms ease-out;
	-moz-transition: color 200ms ease-out;
	-o-transition: color 200ms ease-out;
	transition: color 200ms ease-out;
}

.metadata .date:after {
    margin-left: 5px;
    content: "•"
}

.metadata .contributors {
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none;
}

#main .metadata .contributors li {
    display: inline-block;
    vertical-align: top;
    margin: 0;
    padding: 0;
}

.metadata .contributors-text {
    margin-right: 5px;
}

.metadata .contributors-text:before {
    content: "•";
    margin-right: 5px;
}

.metadata .contributors li img {
    width: 16px;
    height: 16px;
    border-radius: 100%;
    margin-top:5px;
}

@media only screen and (max-width: $desktop-minus1) {
    .metadata {
        margin: 0 0 12px;
    }
}