/* mobile nav */
#menu-nav {
    margin: 24px 0;
	width: 100%;
}

@media only screen and (min-width:769px){
	#menu-nav {
		display: none;	
	}
}

/* TOC v2 */

/*** pdf download ***/
.pdfDownloadHolder
{
    border:1px solid #cfcfcf;
    border-left-width: 0;
    border-right-width: 0;
    margin-top:24px;
    display:none;
}

.theme_night .pdfDownloadHolder
{
    border-color:#2e2e2e;
}

.pdfDownloadHolder a:before {
	font-family: 'FontAwesome';
	content: "\e1fd";
	display: inline-block;
	margin-right: 8px;
	margin-left: -23px;
}

html[dir='rtl'] .pdfDownloadHolder a:before {
    margin-right: -10px;
	margin-left: 8px;
}

/*** toc ***/
.toc{
	margin-top:24px;
	overflow-y: auto;
}

.toc a,
.toc li,
.toc ul
.toc .hideFocus a:focus,
.toc .hideFocus li:focus,
.toc .hideFocus ul:focus {
	outline: 0px solid black;
}

.toc a:focus,
.toc li:focus,
.toc ul:focus {
	outline: 2px solid #0070A7;
}

.toc ul {
	margin-bottom: 0;
}

.no-js .toc li>ul,
.toc li {
	display: none;
	position: relative;
}

.toc li[aria-expanded="true"]>ul>li,
.no-js .toc li[aria-expanded="true"]>a,
.toc li[aria-expanded="true"]>a,
.toc li[aria-expanded="true"]>span,
.toc li[aria-expanded="true"],
.toc>ul>li {
	display: block;
}

.js .toc li>ul {
	height: 0;
	overflow: hidden;
}

.js .toc li[aria-expanded="true"]>ul {
	height: auto;
}

.toc ul {
	padding-left: 1.5rem;
}

.toc ul.noSibs>li>ul {
	padding-left: 1rem;
}

.toc>ul {
	display: block;
	margin-left: 0;
	padding-left: 0;
}

.toc .selectedHolder {
	background-color: $blue;
}

.theme_night .toc .selectedHolder {
	background-color: $night-blue;
}

.toc .selectedHolder>ul {
	background-color: #fff;
	color: #222;
}

.theme_night .toc .selectedHolder>ul {
	background-color: #1a1a1a;
	color: #ccc;
}

.toc .selectedHolder .selected {
	color: #fff;
}

.theme_night .toc .selectedHolder .selected {
	color: #1a1a1a;
}

.toc span,
.toc a,
.pdfDownloadHolder a {
	color: $text;
	display: block;
	font-size: .875rem;
	line-height: 1.3;
	margin-left: 1.8rem;
	margin-right: 1rem;
	padding-bottom: 9px;
	padding-top: 6px;
	word-wrap: break-word;
}

.theme_night .toc span,
.theme_night .toc a {
	color: #ccc;
}

.toc a:link,
.toc a:visited,
.pdfDownloadHolder a:link,
.pdfDownloadHolder a:visited {
	color: $text;
}

.toc a:hover, 
.toc a:active,
.pdfDownloadHolder a:hover,
.pdfDownloadHolder a:active {
	color: #0070a7;
}

.theme_night .toc a:link,
.theme_night .toc a:visited,
.theme_night .pdfDownloadHolder a:link,
.theme_night .pdfDownloadHolder a:visited {
	color: #ccc;
}

.theme_night .toc a:hover, 
.theme_night .toc a:active,
.theme_night .pdfDownloadHolder a:hover,
.theme_night .pdfDownloadHolder a:active {
	color: #0070a7;
}

.toc li.noSubs>ul>li>span,
.toc li.noSubs>ul>li>a {
	margin-left: 1.0rem;
}

.toc ul.noSibs>li>span,
.toc ul.noSibs>li>a {
	margin-left: 1rem;
	margin-right: 1rem;
}

.toc ul.noSibs>li:before {
	display: none !important;
}

.js .toc li.rotate[aria-expanded]:before,
.toc li[aria-expanded]:before {
	color: $text;
	font-family: FontAwesome;
	content: "\e00f";
	font-size: .55rem;
	font-weight: bold;
	left: 12px;
	padding-top: 3px;
	position: absolute;
	-ms-transform: rotate(0deg);
	-webkit-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
}

.theme_night.js .toc li.rotate[aria-expanded]:before,
.theme_night .toc li[aria-expanded]:before {
	color: #ccc;
}

.js .toc li.selectedHolder.rotate[aria-expanded]:before,
.toc li.selectedHolder[aria-expanded]:before {
	color: #fff;
}

.theme_night.js .toc li.selectedHolder.rotate[aria-expanded]:before,
.theme_night .toc li.selectedHolder[aria-expanded]:before {
	color: #1a1a1a;
}

.js .toc li[aria-expanded]:before {
	-webkit-transition: -webkit-transform 200ms ease-out;
	-moz-transition: -moz-transform 200ms ease-out;
	-o-transition: -o-transform 200ms ease-out;
	transition: transform 200ms ease-out;
}

.toc li[aria-expanded="true"]:before {
	font-size: .66rem;
	-ms-transform: rotate(90deg);
	-webkit-transform: rotate(90deg);
	-o-transform: rotate(90deg);
	transform: rotate(90deg);
}

.toc li[aria-expanded]>ul>li[aria-expanded],
.toc li[aria-expanded] {
	cursor: pointer;
}

.toc ul.noSibs>li[aria-expanded] {
	cursor: default;
}

.toc li[aria-expanded]>ul>li,
.toc li[aria-expanded]>span,
.toc ul {
	cursor: default;
}

#menu-nav option.noLink {
	color: #707070;
	font-weight: bold;
}

.theme_night #menu-nav option.noLink {
	color: #ccc;
}

/*** filter ***/
.filterHolder>.emptyFilterMessage,
.tocFilter {
	color: $text;
}

.theme_night .toc>.emptyFilterMessage,
.theme_night .tocFilter {
	color: #ccc;
}

.tocFilter {
	position: relative;
}

.tocFilter>input {
	font-size: 0.875rem;
	height: 36px;
	line-height: 1.8;
	padding: 0 10px;
	width:100%;
}

.filterHolder>.emptyFilterMessage {
	color: $text;
	display: none;
	font-size: 0.875rem;
	margin-top: 23px;
}

.filterHolder.emptyFilter>.emptyFilterMessage {
	display: block;
}

a.clearInput:link {
	position: absolute;
    top:6px;
	right: 4px;
    width: 24px;
    height: 24px;
	display:none;
}

a.clearInput:link .bar {
	stroke:#a9a9a9;
}

a.clearInput:focus{
    outline:1px solid $blue;
}

a.clearInput:focus .bar {
	stroke:$blue;
}

.clearFilter a.clearInput:link {
	display:block;
}

.clearFilter input[type=search] {
	padding-right:24px;
}