@import "config/index";

@import "utilities/index";
@import "base/index";

@import "global";
@import "components/index";
@import "components/doc-metadata";
@import "code/index";
@import "elements/index";
@import "bilingual.css";
@import "impressum";
@import "disclaimer";

/* base page update above this line */
/* updates moved to global.css for now */

.windowsPrerelease {	
	background-color: #f5f5f5;
	border:solid 1px #e0e0e0;
	font-size: 0.875rem;
	padding: 12px 16px 2px 16px;
	margin-top:16px;
}

.windowsPrerelease p {
	margin-top:0;
}

.theme_night .windowsPrerelease {
    background-color: #222324;
	border-color: #333;
}

.prerelease{
    color: $red;
    font-weight:bold;
}

.memberDetails .prerelease{
    padding-right:8px;
}

.mainContainer[dir='rtl'] .memberDetails .prerelease{
    padding-left:8px;
    padding-right:0;
}

div.inheritedMembers div {
    display: inline;
}

mark {
    font-size: 0.8em;
    vertical-align: baseline;
    padding: 0 0.45em 0.1em 0.45em;
    border-radius: 2px;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(0,0,0,0.05);
    background-color: rgba(0,0,0,0.05);
    color: #333
}

.theme_night mark {
    border-color: rgba(255,255,255,0.05);
    background-color: rgba(255,255,255,0.03);
    color: #ccc
}

.theme_night code {
    border-color: rgba(255,255,255,0.05);
    background-color: rgba(255,255,255,0.03)
}

.theme_night label {
    color: #ccc
}

blockquote {
    display: block;
    margin: 1.2em 0 2em 0;
    padding: 0 0.8em;
    border-width: 0 0 0 3px;
    border-style: solid;
    border-color: #e0e0e0
}

.theme_night blockquote {
    border-color: #404040
}

blockquote footer {
    font-size: 0.9em;
    color: gray
}

.theme_night blockquote footer {
    color: gray
}

.definition {
    border-width: 0 0 1px 0;
    border-style: dotted;
    border-color: #ababab
}

.theme_night .definition {
    border-color: #6e6e6e
}

.full {
    margin-left: 0;
    padding-left: 0;
    width: 100%;
    clear: both
}

.mainContainer[dir='rtl'] .full {
    margin-right: 0;
    padding-right: 0;
}

.overflowable {
    overflow-x: auto;
    min-height: 0.01%;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch
}

figure {
    margin: 0.6em 2em 0.6em 0;
}

@media only screen and (min-width: $tablet) {
    figure {
        margin: 0.6em 0
    }
}

figure figcaption {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
    font-size: 0.85em;
    line-height: 1.4em;
    color: gray
}

.theme_night figure figcaption {
    color: gray
}

figure>img,figure>video {
    padding: 0.6em;
    border-radius: 2px;
    border-width: 1px;
    border-style: solid;
    border-color: #e3e3e3
}

.theme_night figure>img,.theme_night figure>video {
    border-color: #2e2e2e
}

figure.no-border>img,figure.no-border>video {
    border: 0;
    padding: 0
}

img,video {
    max-width: 100%;
    height: auto;
    display: inline-block;
}

.theme_night pre {
    border-color: rgba(255,255,255,0.05);
    color: #ddd;
    background-color: #242424
}

.theme_night pre code {
    background-color: transparent
}

.list-indent li {
    margin-left: 0.6em;
}

.mainContainer[dir='rtl'] .list-indent li {
    margin-left: 0;
    margin-right: 0.6em;
}

.list-footnotes {
    font-size: 0.9em;
    color: gray;
}

.theme_night .list-footnotes {
    color: gray;
}

.table-wrapper {
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    margin: 0.6em 0 1.2em 0;
    border-color: #e3e3e3;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    overflow-x: auto;
    min-height: 0.01%;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch
}

.theme_night .table-wrapper {
    border-color: #2e2e2e
}

.memberDetails header,.memberDetails section {
    overflow: auto
}

.memberDetails header {
    font-size: 0.9em
}

.theme_night .memberDetails>li {
    border-color: rgba(255,255,255,0.05)
}

.theme_night .memberDetails .meta li code {
    background-color: transparent
}

.theme_night .memberDetails .description code {
    background-color: rgba(255,255,255,0.03)
}

.expander code, .memberDetails code {
    word-break: break-all;
}

.memberDetails .tags {
    vertical-align: top;
    clear: both;
    padding: 0;
    padding-top: 0.6em;
    padding-bottom: 0.2em;
    list-style-type: none
}

.memberDetails .tags li {
    display: inline-block;
    margin: 0;
    padding: 0;
    padding: 0.4em 0.6em;
    height: 2em;
    font-size: 0.6em;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 1em;
    text-align: center;
    vertical-align: top;
    border-radius: 2px;
    border-width: 1px;
    border-style: solid;
    background-color: gray;
    color: #fcfcfc;
    border-color: rgba(0,0,0,0.05)
}

.theme_night .memberDetails .tags li {
    background-color: gray;
    color: #1a1a1a;
    border-color: rgba(255,255,255,0.05)
}

.memberDetails .tags .round {
    border-radius: 100%;
    width: 2em
}

.memberDetails .tags .off {
    font-weight: 400;
    text-shadow: none;
    background-color: rgba(0,0,0,0.05);
    color: #ababab
}

.theme_night .memberDetails .tags .off {
    background-color: rgba(255,255,255,0.03);
    color: #6e6e6e
}

/* Highlight the http verb */
.method-http-verb {
  color: $text-light;
}

/* product feedback */
.product-feedback {
    margin-bottom: 2em;
    margin-top: 2em;
    margin-left: 10px;
    padding-top: 0.6em;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #e3e3e3;
    color: gray;
}

.mainContainer[dir='rtl'] .product-feedback {
    margin-left: 0px;
    margin-right: 10px;
}