#uhfPlaceHolder
{
    height: 120px;
}

@media only screen and (min-width: 1779px) {
    #uhfPlaceHolder {
      margin-top: 12px;
    }
}

.m-auto-suggest{
	display:none !important;
}

#headerArea {
    line-height: normal;
}

a.searchScope:link {
	position: absolute;
	left: 0;
	background-color: #d5e0ff;
    background-image: url(../images/close_grey.png);
    background-position: right 10px;
    background-repeat: no-repeat;
	padding: 5px 20px 4px 3px;
	margin: 3px;
	color: $text;
	max-width: 120px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

a.searchScope:focus{
    outline:1px solid $blue;
}

@media only screen and (max-width: 1083px) {
    a.searchScope{
        display:none;
    }

    div.f-search-opened a.searchScope {
        display:block;
    }
}

#sub-header {
    box-sizing: content-box !important; /* combatting azure header */
    clear: both;                
    margin: 0 auto;
    max-width:1600px;
    min-height: 25px;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;       
    padding:0 5%;
    width:auto;
}

@media only screen and (max-width: 767px) {
    #sub-header {
        padding: 8px 16px;
    }
}

.breadcrumbs {
    margin: 0;
    padding: 8px 0;
    font-size: 0.8125rem;    
    list-style: none;
}

.breadcrumbs li {
    display: inline-block;
    margin: 0
}

.theme_night .breadcrumbs li:after {
    color: #6e6e6e;
}

.breadcrumbs li:last-child {
    color: $text;
}

.theme_night .breadcrumbs li:last-child {
    color: gray
}

.breadcrumbs li:last-child:before,.breadcrumbs li:last-child:after {
    content: '';
}

html[dir='rtl'] .breadcrumbs li:last-child:before,.breadcrumbs li:last-child:after {
    content: ' ';
}

.breadcrumbs li:after {
    content: '/';
    vertical-align: baseline;
    padding: 0 8px;
    color: #ababab
}