@import "font";

html {
    font-size: 100%;
    overflow-y: scroll;
}

code {
    direction: ltr;
}

.textAccent1 {
    color:$text-darkOrange;
}

.textAccent2 {
    color:$text-teal;
}

.bgdAccent1 {
    background-color: #E4EDF1
}

span.likeAlabel {
	color: $text-subtle;
	font-style: italic;
}

.theme_night span.likeAlabel {
	color: $night-text-subtle;
}

div.button a, div.step-by-step a {
    position: relative;
    display: inline-block;
    font-size: 0.7em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 600;
    vertical-align: top;
    padding: 0.2em 0.7em;
    border-radius: 2px;
    border: 0;
    background-color: #007bb8;
    color: #fcfcfc
}

.theme_night div.button a, .theme_night div.step-by-step a {
    background-color: #007bb8;
    color: #1a1a1a
}

div.button a:focus, div.step-by-step a:focus {
    background-color: #0070a7;
    color: #f5f5f5
}

div.button a:hover, div.step-by-step a:hover {
    background-color: #0070a7;
    color: #f5f5f5
}

.theme_night div.button a:hover, .theme_night div.step-by-step a:hover {
    background-color: #0070a7;
    color: #242424;
}

div.step-by-step a {
    margin: 0.1em 0.5em;
}

.theme_night div.step-by-step {
    border-color: #2e2e2e;
}

div#headerWrapper {
    height: 96px;
}

.list-clean {
    padding: 0;
    list-style: none
}

.list-clean>li {
    margin-left: 0;
}

.mainContainer[dir='rtl'] .list-clean>li {
    margin-right: 0;
}

/* Make hidden attribute compatible with old browser.*/
[hidden] {
    display: none !important;
}

/* Container and iFrame styles for embedded video */
div.embeddedvideo {
    padding-top: 56.25%; 
    position: relative; 
    width: 100%;
}

div.embeddedvideo iframe {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}

.codeHeader {
    background-color:#f5f5f5;
    color:#707070;
    font-size: 0.8rem;
    border:solid 1px #e0e0e0;
    border-bottom:0;
    margin-top:10px;
}

.theme_night .codeHeader {
    border-color: #333;
    background-color: #222324;
    color: #ccc;
}

.copyBtn {
    padding: 2px 10px;
    cursor: pointer;
	float: right;
    border-left:solid 1px #e0e0e0;
}

.mainContainer[dir='rtl'] .copyBtn {
	float: left;
    border-left:solid 0px #e0e0e0;
    border-right:solid 1px #e0e0e0;
}

.copyBtn:hover
{
    background-color:#e6e6e6;
    color:#222;
}

.theme_night .copyBtn
{
    border-color: #333;
}

.theme_night .copyBtn:hover {
    background-color: #333;
    color: #fff;
}

.codeHeader .labelHolder {
    padding: 2px 16px;
}

.codeHeader+pre{
    clear:right;
    margin-top:0;
}

.mainContainer[dir='rtl'] .codeHeader+pre{
    clear:left;
}

.fixed {
    position: fixed;
    top: 0;
    z-index: 10;
    overflow-y: auto;
}

.centered{
    text-align:center;
}

.pageFooter{
    border-top: 1px solid #e0e0e0;
    margin-top:48px;
    padding:24px 0;    
}

html.theme_night .pageFooter {
	border-top: 1px solid #2e2e2e;
}

.pageFooter ul.links {
    list-style-type: none;
}

.pageFooter ul.links li {
    display: inline-block;
    padding:0 25px;
}

.pageFooter ul.links li a {
    color:#222;
    font-family: segoe-ui_semibold, 'Segoe UI Semibold', 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;
}

html.theme_night ul.links li a {
    color:#ccc;
}

.pageFooter ul.links li a:hover {
    color:$blue;
    text-decoration:underline;
}

/* adding ref classes here until page templates are cleaned up */

div.summary {	
    margin-bottom:24px;
	margin-top: 12px;
}

h2.cli {
	color: #4c668b;
	font-size:1.875rem;
	font-family: segoe-ui_semibold;
}

h3.cli {
	font-size:1.4375rem;
}

ul.inheritance {
    margin: 0;
    padding: 0;
    margin-left: 8px;
    list-style-type: none;
    list-style-position: inside
}

.mainContainer[dir='rtl'] ul.inheritance {
    margin-left: 0;
    margin-right: 8px;
}

ul.inheritance a {
    border: 0
}

ul.inheritance div {
    display:inline-block;
}

ul.inheritance li {
    margin: 0;
    padding: 0;
    font-size:0.9375rem;
}

ul.inheritance .l0 {
    padding-left: 0
}

.mainContainer[dir='rtl'] ul.inheritance .l0 {
    padding-right: 0
}

ul.inheritance .l1 {
    padding-left: 16px;
}

.mainContainer[dir='rtl'] ul.inheritance .l1 {
    padding-right: 16px;
}

ul.inheritance .l2 {
    padding-left: 32px;
}

.mainContainer[dir='rtl'] ul.inheritance .l2 {
    padding-right: 32px;
}

ul.inheritance .l3 {
    padding-left: 48px;
}

.mainContainer[dir='rtl'] ul.inheritance .l3 {
    padding-right: 48px;
}

ul.inheritance .l4 {
    padding-left: 64px;
}

.mainContainer[dir='rtl'] ul.inheritance .l4 {
    padding-right: 64px;
}

ul.inheritance .l5 {
    padding-left: 80px;
}

.mainContainer[dir='rtl'] ul.inheritance .l5 {
    padding-right: 80px;
}

ul.inheritance .l6 {
    padding-left: 96px;
}

.mainContainer[dir='rtl'] ul.inheritance .l6 {
    padding-right: 96px;
}

ul.inheritance .l7 {
    padding-left: 112px;
}

.mainContainer[dir='rtl'] ul.inheritance .l7 {
    padding-right: 112px;
}

ul.inheritance .l8 {
    padding-left: 128px;
}

.mainContainer[dir='rtl'] ul.inheritance .l8 {
    padding-right: 128px;
}

ul.inheritance .l9 {
    padding-left: 144px;
}

.mainContainer[dir='rtl'] ul.inheritance .l9 {
    padding-right: 144px;
}

.memberGroup h4, .inheritedMembers h4 {
    font-size:1rem;
    margin:24px 0 6px 0;
}

.memberGroup p{
    margin:0;
}

.memberGroup ul.list-clean>li {
    margin-bottom:48px;  
}

.memberGroup ol {
    margin: 6px 0 12px 24px;
}

.memberName {
    border-bottom:solid 1px #e0e0e0;
    font-size: 1.25rem;
    font-family: segoe-ui_semibold, 'Segoe UI Semibold', 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;
    padding-bottom:4px;
}

.theme_night .memberName{
    border-color: #2e2e2e;
}

.memberName+p {
    margin-top:10px;
}

.memberName div {
	word-wrap: break-word;
}

.memberEditButton {
    float:right;
	font-size: 0.875rem;
    padding:0 12px 0 24px;
}

.mainContainer[dir='rtl'] .memberEditButton {
    float:left;
	padding:0 24px 0 12px;
}

.memberEditButton a, .memberEditButton a:link {
    color:$text-subtle;
}

.theme_night .memberEditButton a, .theme_night .memberEditButton a:link {
    color: $night-text-subtle;
}

.memberEditButton a:hover {
    color:$blue;
}

.theme_night .memberEditButton a:hover {
    color: $night-blue;
}

.memberEditButton a i {
    color: $blue;
	padding-right: 6px;
}

.mainContainer[dir='rtl'] .memberEditButton a i {
	padding-left: 6px;
	padding-right: 0;
}

.memberDeclaration{
    margin-top:10px;
    margin-bottom:12px;
}

#main .memberDetails
{
    list-style-type: none;
}

#main .memberDetails>li{
    margin:0 0 18px;
}

#main .memberDetails .single {
    margin-bottom: 4px;
    color:#6e6e6e;
}

.memberDetails dl {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    margin:0;
}

.memberDetails dt {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding-right:12px;
}

.mainContainer[dir='rtl'] .memberDetails dt {
    padding-left:12px;
    padding-right:0;
}

.memberDetails dd {
    -webkit-flex: 5;
    -ms-flex: 5;
    flex: 5;
    margin:0;
    color:#6e6e6e;
}

.memberDetails h4+p {
    margin:8px 0;
}

.detailTile {
    width:50%;
    float:left;   
    margin-bottom: 28px;
}

.mainContainer[dir='rtl'] .detailTile {
    float:right;   
}

.detailTile h4 {
    font-size:1rem;
    margin:0 0 6px 0;
}

.detailTile div {
    color:#6e6e6e;
}

.detailTile+h2 {
    clear:left;
}

.mainContainer[dir='rtl'] .detailTile+h2 {
    clear:right;
}

.parameterName {
	background-color: #e8ebf0;
	display: inline-block;
	font-family: Consolas, Menlo, Monaco, 'Lucida Console', 'Liberation Mono', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Courier New', monospace, sans-serif;
	font-weight: bold;
	padding: 0 6px 2px 6px;
}

.theme_night .parameterName {
	background-color: #4c668b;
	color:#fff;
}

.parameterSummary, .parameterInfo {
	color: $text-subtle;
	margin-left:24px;
}

.theme_night .parameterSummary, .theme_night .parameterInfo {
	color: $night-text-subtle;
}

.parameterSummary .nameValue {
	margin-bottom: 24px;
}

.parameterName + .parameterInfo {
    margin-top: 4px;
}

.parameterInfo div {
	border-right:solid 1px $text-subtle;
	display: inline-block;	
	margin-right:8px;
	padding-right:12px;
}

.parameterInfo div:last-child {
	border-right:0;
	display: inline-block;	
	margin-right:0;
	padding-right:0;
}

.theme_night .parameterInfo div {
	border-color:$night-text-subtle;
}

.globalParams>a {
	padding-left:24px;
}

.globalParams>a:before {
    color: #222;
    font-family: FontAwesome;
    content: "\e00f";
    font-size: .8rem;
    font-weight: bold;
    left: 0;
    padding-top: 1px;
    position: absolute;
	-webkit-transition: -webkit-transform .2s ease-out;
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out,-webkit-transform .2s ease-out;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}

.globalParams[aria-expanded=true]>a:before {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}

.globalParams ul {
	display: none;
	margin-top:12px;
}

.globalParams[aria-expanded=true] ul {
	display: block;
}

/* #main table */

#main table {
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: auto;
    width: 100%;
    font-size: 0.875rem;
}

@media only screen and (max-width: $tablet) {
    #main table {
        table-layout: fixed;
    }
}

#main table caption {
    font-size: 0.8em;
    font-weight: 600;
    text-align: left;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 1.2em;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: #e3e3e3
}

#main table caption {
    text-align: right;
}

.theme_night #main table caption {
    border-color: #2e2e2e
}

#main td, #main th {
    display: table-cell;
    word-wrap: break-word;
    padding: 12px 16px;
    line-height: 1.18;
    vertical-align: top;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #e3e3e3;
}

#main td.nowrap, #main th.nowrap {
    white-space: nowrap;
}

.theme_night #main td, .theme_night #main th {
    border-color: #2e2e2e
}

#main th {
    text-align: left;
    font-family: segoe-ui_semibold, 'Segoe UI Semibold', 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 0.875rem;
    font-weight: normal;
    vertical-align: bottom;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: #e3e3e3;
}

.mainContainer[dir='rtl'] #main th {
    text-align: right;
}

#main table>caption+thead>tr:first-child>td,
#main table>colgroup+thead>tr:first-child>td,
#main table>thead:first-child>tr:first-child>td {
    border-top: 0
}

#main .table-striped>tbody>tr:nth-of-type(odd) {
    background-color: rgba(0,0,0,0.05)
}

.theme_night #main .table-striped>tbody>tr:nth-of-type(odd) {
    background-color: rgba(255,255,255,0.03)
}

/* table.nameValue */

#main table.nameValue {
    border-width: 0;
    table-layout: fixed;
}

#main table.nameValue tr:only-child > td {
    border:0;
    padding-top:0;
}

#main table.nameValue td {    
    border-color: #e0e0e0;
    padding:12px 0;
}

.theme_night #main table.nameValue td {
    border-color: #2e2e2e;
}

#main table.nameValue code {    
    word-break: break-all;
}

#main table.nameValue td:nth-child(1) {
    float:left;
    padding:14px 0 0 0;
    width:100%;
}

.mainContainer[dir='rtl'] #main table.nameValue td:nth-child(1) {
    float:right;
}

#main table.nameValue td:nth-child(2) {
    border-width: 0;
    float:left;    
    padding:0 0 14px 0;
}

.mainContainer[dir='rtl'] #main table.nameValue td:nth-child(2) {
    float:right;    
}

#main table.nameValue td:only-child {
    float:left;
    padding:14px 0;
    width:100%;
}

.mainContainer[dir='rtl'] #main table.nameValue td:only-child {
    float:right;
}

#main table.nameValue td p {
    margin:0;
}

@media only screen and (min-width: $tablet) {
    #main table.nameValue td:nth-child(1) {
        float:none;
        padding:14px 28px 14px 0;
        width:30%;
        min-width: 200px;
    }

    #main table.nameValue td:nth-child(2) {
        float:none;
        padding:14px 0;
        width:70%;
        border-top-width: 1px;
    }

    #main table.nameValue td:only-child {
        float:none;
        padding:14px 0;
        width:100%;
    }
}

#main .memberDetails table.nameValue td:nth-child(1) {
    padding:8px 0 0 0;
}

#main .memberDetails table.nameValue td:nth-child(2) {   
    padding:0 0 8px 0;
}

#main .memberDetails table.nameValue td:only-child {
    padding:8px 0;
}

@media only screen and (min-width: $tablet) {
    #main .memberDetails table.nameValue td:nth-child(1) {
        padding:8px 16px 8px 0;
    }

    #main .memberDetails table.nameValue td:nth-child(2) {
        padding:8px 0;
    }

    #main .memberDetails table.nameValue td:only-child {
        padding:8px 0;
    }
}

/* dl.nameValue */
dl.nameValue{
    font-size: 0.875rem;
    margin-bottom:0;
}

dl.nameValue dt{
    min-width: 110px;    
    border-top:solid 1px #e0e0e0;
    padding:4px 4px 0 4px;
    color:#6e6e6e;
}

dl.nameValue dd:last-of-type {
    border-bottom:solid 1px #e0e0e0;
}

dl.nameValue dd {
    min-width: 110px;
    margin: 0;
    padding:0 4px 4px 4px;    
}

dl.nameValue dd.expanded {
    border-bottom:0;
}

dl.nameValue div.extended {
    float:left;
    border-left:solid 1px #e0e0e0;
    border-bottom:solid 1px #e0e0e0;
    padding:4px 10px;
    display:none;
}

dl.nameValue::after{
    clear:left;
    content:' ';
    display:block;
    padding-top:1em;
}

dl.nameValue .object p{
    font-size:1rem;
}

@media only screen and (min-width: $tablet) {
    dl.nameValue dt{
        clear:left;
        float:left;
        padding:4px;
    }

    .mainContainer[dir='rtl'] dl.nameValue dt{
        clear:right;
        float:right;
    }

    dl.nameValue dd {
        float:left;
        min-width: 190px;
        border-top:solid 1px #e0e0e0;
        padding:4px 10px;  
    }

    .mainContainer[dir='rtl'] dl.nameValue dd {
        float:right;
    }

    dl.nameValue dt.expanded {
        border-right:solid 1px #e0e0e0;
    }

    .mainContainer[dir='rtl'] dl.nameValue dt.expanded {
        border-left:solid 1px #e0e0e0;
        border-right:solid 0px #e0e0e0;
    }

    .mainContainer[dir='rtl'] dl.nameValue dt.expanded {
        border-left:solid 1px #e0e0e0;
        border-right:solid 0px #e0e0e0;
    }

    dl.nameValue dt:last-of-type {
        border-bottom:solid 1px #e0e0e0;
    }

    dl.nameValue div.extended {
        display:block;
    }

    dl.nameValue .object{
        margin-left: -96px;
    }

    .mainContainer[dir='rtl'] dl.nameValue .object{
        margin-left: 0;
        margin-right: -96px;
    }
}