$gutter: 0; /* disable minus margin  */

@define-mixin grids {
	margin-left: -$(gutter);
	margin-right: @margin-left;
	margin-top: @margin-left;
	&:last-child {
		margin-bottom: -$(gutter);
	}
	&:not(:last-child) {
		/*margin-bottom: $gutter;*/
		margin-bottom: 0;
	}
	/*margin: 0;*/
	/* Responsive */
	& {
		@mixin tablet {
			&:not(.is-desktop) {
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
			}
		}
		@mixin desktop {
			display: -webkit-box;
			display: -moz-box;
			display: -ms-flexbox;
			display: -webkit-flex;
			display: flex;
		}
	}
}

/* TODO: this is not working correctly, $ are ending up in final CSS */
.column {
	-webkit-flex: 1;
	-ms-flex: 1;
	flex: 1;	
	/* 12 grids */
	@for $i from 1 to 11 {
		&.is-$i {
			-webkit-flex: none;
			-ms-flex: none;
			flex: none;	
			width: calc($i / 12 * 100)%;
		}
		&.is-offset-$i {
			margin-left: calc($i / 12 * 100)%;
		}
	}
	/* TODO: Responsive variations */
	& {
		@mixin mobile {

		}
		/* Tablet is our default choice */
		@mixin tablet {
			@for $i from 1 to 11 {
				$.is-$(i)-tablet {
					-webkit-flex: none;
					-ms-flex: none;
					flex: none;	
					width: calc($i / 12 * 100%);
				}
				$.is-offset-$(i)-tablet {
					margin-left: calc($i / 12 * 100%);
				}
			}
		}
		@mixin desktop {

		}
	}
}

.columns {
	@mixin grids;
}
