#footer {
    position: relative;
    bottom: 0;
    left: 0;
    padding: 48px 0 40px;
    border-width: 1px 0 0 0;
    border-style: solid;
    background-color: #1a1a1a;
    border-color: #e3e3e3;
    margin-top:40px;
}

.theme_night #footer {
    background-color: #1a1a1a;
    border-color: #2e2e2e
}

#footer .container:first-child {
    padding: 0 5%;
    width:auto;
}

#footer .links {
    font-size: 0.8125rem;
    margin-top: 16px;
}

#footer .links li {
    display: inline-block;
    margin-left: 0.2em;
    margin-right: 0
}

#footer .links li:last-child {
    margin-right: 0
}

#footer .links li:last-child:after {
    content: none
}

#footer .links li:after {
    margin-left: 8px;
    content: "•";
    color: #ababab
}

html[dir='rtl'] #footer .links li::after {
    margin-right: 8px;
}

#footer .links a {
    color: #3d88c0;
}

.theme_night #footer .links li:after {
    color: #6e6e6e
}

@media only screen and (min-width: $tablet) {
    #footer .links {
        margin-top: 0;
        float:right;
    }

    html[dir='rtl'] #footer .links {
        float:left;
    }
}