.doc-outline h3{
    color: $text;
    font-size: 1rem;
    margin: 0;
    margin-top: 16px;
    display:none;
}

.theme_night .doc-outline h3{
    color:#828282;
}

.doc-outline ul {
    margin: 10px 0 0 0;
    list-style-type: none;
}

.doc-outline li {
    margin:10px 0;
    line-height: 1.3;
    font-size: 0.875rem;
}
