/*

Visual Studio-like style based on original C# coloring by Jason Diamond <jason@diamond.name>

*/
.line-highlight {
  background-color: #ffc;
}

.hljs-comment
{
  color: #008000;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-built_in,
.hljs-name,
.hljs-tag {
  color: #0101FD;
}

.hljs-literal
{
  color: #09885a;
}

.hljs-string,
.hljs-section,
.hljs-template-tag,
.hljs-template-variable,
.hljs-addition,
.hljs-code {
  color: #a31515;
}

.hljs-parameter,
.hljs-type,
.hljs-attribute,
.hljs-title,
.hljs-deletion,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-meta {
  color: #007d9a;
}

.hljs-doctag {
  color: #737373;
}

.hljs-attr {
  color: #f00;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link {
  color: #00b0e8;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}