.pageActionsHolder{
    box-sizing: content-box !important; /* combatting azure header */
}

#page-actions{
    font-size: 0.875rem;
    z-index: 52;    
}

#page-actions ol, #page-actions ul {    
    margin-bottom: 0;
    list-style: none;
    padding: 0 !important; /* combatting livefyre */
    list-style-type: none;
}

#page-actions ol {
    margin-top:10px;
}

.action-list li, .doc-outline ol li {
    margin:0;
    padding-bottom:8px;    
}

.action-list a, .action-list a:link {
    color:$text-subtle;
}

.theme_night .action-list a, .theme_night .action-list a:link {
    color: $night-text-subtle;
}

.action-list a:hover {
    color:$blue;
}

.theme_night .action-list a:hover {
    color: $night-blue;
}

.action-list a i {
    color:$blue;
    padding-right: 6px;    
}

#page-actions label{
    font-family: segoe-ui_semibold, 'Segoe UI Semibold', 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;    
    font-size:0.7rem;
    line-height: 1.3;
    text-rendering: optimizeLegibility;
    margin-bottom: 6px;
    margin-top: 10px;
    word-wrap: break-word;
    color: $text-subtle;
}

.theme_night #page-actions label {
	color: $night-text-subtle;
}

#branch-selector{
    max-width:100%;
}

#side-doc-outline{
    display:none;
}

#center-doc-outline{
    display:block;
}

#main #center-doc-outline ol {
    list-style-type: none;
    margin-top:10px;
}

#main #center-doc-outline ol li {
    margin:0;
}

@media only screen and (max-width: $desktop-minus1) {
    .pinned-actions{
        max-height: none !important;
    }

    ul.action-list li{
        display:inline-block;
        padding-right:8px;
    }
}

@media only screen and (min-width: $desktop) {
    .pinned-actions{
        position: fixed;
        top: 0;
        z-index: 10;
        overflow-y: auto;
    }

    #page-actions nav, #page-actions ul {    
        margin-left:10px;
    }

    #page-actions .typeSep {
        display:none;
    }

    #page-actions label{
        display: block;
    }

    #side-doc-outline{
        display:block;
    }

    #center-doc-outline{
        display:none;
    }
}