
.dropdown-container {
    overflow: visible;
    display: inline-block;
    vertical-align: top;
    padding: 0;
}

.dropdown {
    display: inline-block;
    position: relative
}

.dropdown-small {
    max-width: 100px;
    width: 100px
}

.dropdown-small .dropdown,.dropdown-small select {
    max-width: 100px;
    width: 100px
}

.dropdown-medium {
    max-width: 180px;
    width: 180px
}

.dropdown-medium .dropdown,.dropdown-medium select {
    max-width: 180px;
    width: 180px
}

.dropdown-large {
    max-width: 280px;
    width: 280px
}

.dropdown-large .dropdown,.dropdown-large select {
    max-width: 280px;
    width: 280px
}

.dropdown-full {
    width: 100%;
    max-width: 100%
}

.dropdown-full .dropdown,.dropdown-full select {
    width: 100%;
    max-width: 100%
}