main>.container:first-child {
    padding: 0 5%;
    width:auto;
}

@media only screen and (max-width: $tablet-minus1) {
    main>.container:first-child  {
        padding: 0 16px;
        width:auto;
    }
}

/* Right Column */
.pageActionsHolder{  
    margin: 0 auto;  
    max-width: 1600px;
    padding: 8px 16px;    
    position:relative;
}

@media only screen and (min-width: $tablet) {
    .pageActionsHolder{
        padding: 0 5%;
    }
}

@media only screen and (min-width:$desktop){
    #page-actions {        
        position:absolute;
        right: 5%;
        width: 230px;
        margin-top:18px;
    }
}

/* END Right Column */

/* Left Column */

#sidebar {
    display: none;
    float: left;    
    width: 220px;
}

@media only screen and (min-width: $tablet) {
    #sidebar {        
        display:block;        
        min-height: 1px;/*hack to fix float and delayed rendering in Chrome */
    }
}

.filterHolder {
	padding-top: 24px;
}

.fixed .filterHolder {
	padding-top: 0;
}

/* END Left Column */

/* Center Column */

#main {
    margin-left: 0;
    margin-right: 0;
    position: relative;
}

#comments-container {
    margin-top:60px;
}

@media only screen and (min-width: $tablet) {
    #main {
        margin-left: 244px;
        margin-right: 0;
    }
}

@media only screen and (min-width: $desktop) {
    #main {
        margin-left: 244px;
        margin-right: 244px;
    }
}

/* END Center Column */