.alert {
    padding: 16px;
    margin-top: 8px;
    margin-bottom: 14px;
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    font-size: 0.875rem;
    line-height: 1.4;
    border-color: rgba(0,0,0,0.05);
    background: #f5f5f5;
    color: #333
}

.theme_night .alert {
    border-color: rgba(255,255,255,0.05);
    background: #242424;
    color: #fff
}

.alert h5 {
    font-size:1rem;
}

.alert>:first-child {
    margin:0 0 8px 0;
    padding:0;
}

.alert>:last-child {
    margin:0;
    padding:0;
}

.NOTE {
    border-color: #0070a7;
}

.alert a {
    color: #0070a7;
}

.CAUTION a,
.WARNING a {
    color: #006ba0;
}

.theme_night .NOTE {
    border-color: #4e95cc;
    background: #002c41;
    color: #4e95cc;
}

.NOTE h5 {
    color: #0070a7;
}

.NOTE h5:before {
    content: "\e70b";
    margin-right: 4px;
}

.theme_night .NOTE h5 {
    color: #4e95cc;
}

.IMPORTANT {
    border-color: #8a6d3b;
    background: #fcf8e3;
    color: #8a6d3b;
}

.theme_night .IMPORTANT {
    border-color: #aa8d63;
    background: #322b05;
    color: #aa8d63;
}

.IMPORTANT h5 {
    color: #8a6d3b;
}

.IMPORTANT h5:before {
    content: "\e783";
    margin-right: 4px;
}

.theme_night .IMPORTANT h5 {
    color: #aa8d63
}

.CAUTION, 
.WARNING {
    border-color: #a94442;
    background: #f2dede;
    color: #a94442
}

.theme_night .CAUTION,
.theme_night .WARNING {
    border-color: #ce7d7c;
    background: #4d1e1e;
    color: #ce7d7c;
}
.CAUTION h5,
.WARNING h5 {
    color: #a94442;
}

.CAUTION h5:before {
    content: "\e783";
    margin-right: 4px;
} 

.WARNING h5:before {
    content: "\e945";
    margin-right: 4px;
}

.theme_night .CAUTION h5,
.theme_night .WARNING h5 {
    color: #ce7d7c;
}

.TIP {
    border-color: #3c763d;
    background: #dff0d8;
    color: #3c763d;
}

.theme_night .TIP {
    border-color: #6d8e6e;
    background: #14230d;
    color: #6d8e6e;
}

.TIP h5 {
    color: #3c763d
}

.TIP h5:before {
    content: "\e82f";
    margin-right: 4px;
}

.theme_night .TIP h5 {
    color: #6d8e6e
}

.alert-minimal {
    border-color: rgba(0,0,0,0.05);
    background: #fff;
    color: #333
}

.theme_night .alert-minimal {
    border-color: rgba(255,255,255,0.05);
    background: #1a1a1a;
    color: #fff
}

.alert-minimal h5 {
    color: #333
}

.theme_night .alert-minimal h5 {
    color: #fff
}